import React from 'react'
import {FormGroup, Input,Col} from "reactstrap";


function InputField({align,md,label,placeholder,type,name,value,onChange}) {

  return(
    <React.Fragment>
      <Col className={align} md={md}>
        <FormGroup>
          <label>{label}</label>
          <Input
            placeholder={placeholder}
            type={type}
            name={name}
            value={value}
            onChange={onChange}
          />
        </FormGroup>
      </Col>
    </React.Fragment>
  );
}


export default InputField;
