
import React from 'react'

import { List } from './'

import api from '../../appstate/api';


export class WizcardTwo extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedCandidate:'',
      candidates: []
    }

    this.selectCandidate = this.selectCandidate.bind(this)
  }
  componentWillMount(){    
    this.setState({candidates: api.utils.findcandidates('VPZ')})
  }


  selectCandidate(rd){
    // const selected = !rd.selected;
    // rd.selected = selected;
    let votes = JSON.parse(localStorage.getItem('votes'))
    votes.vpz = rd
    localStorage.setItem('votes',JSON.stringify(votes))
    this.setState({ selectedCandidate: rd });
  }

  render () {
    // const { data } = this.props;
    // var cards = this.renderList(data)
    // console.log(this.state.selectedCandidate);
    return (
      <div>

        <div className='row'>
        {this.state.candidates.map(candidate => {
            return (
              <List
                key={`dd-item-${candidate.rid}`}
                nam={candidate.nam}
                pic={candidate.pic}
                selected={candidate}
                selectedbgcolor='purple'
                bgcolor="white"
                selectCandidate={this.selectCandidate}
                selectedCandidate={candidate === this.state.selectedCandidate}
                cnm={this.props.cnm}
                ballot={this.props.ballot}
              />
            );
          })}
        </div>
      </div>
    )
  }
}
