import React,{ useState, useEffect,useContext} from "react";
import { NavLink, Link } from "react-router-dom";
// nodejs library to set properties for components


// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";

// reactstrap components
import { Nav } from "reactstrap";

var ps;
import api from '../../appstate/api';
import {AppContext} from '../../appstate/appcontext';


function Sidebar(props){

  const { state, dispatch,history } = useContext(AppContext)
  let sidebar = React.createRef()


  // verifies if routeName is the one active (in browser input)
  const activeRoute = (routeName) => {
    return props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }

  const activeMenu = (smenu) => {
    dispatch({type:'SUB_MENUS',payload:smenu});

  }

  useEffect(() => {
    // if (navigator.platform.indexOf("Win") > -1) {
    //   ps = new PerfectScrollbar(sidebar, {
    //     suppressScrollX: true,
    //     suppressScrollY: false
    //   });
    // }
    return function cleanup(){
      if (navigator.platform.indexOf("Win") > -1) {
        //ps.destroy();
      }
    }
  })

  const linkOnClick = () => {
    document.documentElement.classList.remove("nav-open");
  };

  const { bgColor, routes, rtlActive, logo,toggleSidebar } = props;

  let logoImg = null;
  let logoText = null;
  if (logo !== undefined) {
    if (logo.outterLink !== undefined) {
      logoImg = (
        <a
          href={logo.outterLink}
          className="simple-text logo-mini"
          target="_blank"
          onClick={toggleSidebar}
        >
          <div className="logo-img">
            <img src={logo.imgSrc} alt="react-logo" />
          </div>
        </a>
      );
      logoText = (
        <a
          href={logo.outterLink}
          className="simple-text logo-normal"
          target="_blank"
          onClick={toggleSidebar}
        >
          {logo.text}
        </a>
      );
    } else {
      logoImg = (
        <Link
          to={logo.innerLink}
          className="simple-text logo-mini"
          onClick={toggleSidebar}
        >
          <div className="logo-img">
            <img src={logo.imgSrc} alt="react-logo" />
          </div>
        </Link>
      );
      logoText = (
        <Link
          to={logo.innerLink}
          className="simple-text logo-normal"
          onClick={toggleSidebar}
        >
          {logo.text}
        </Link>
      );
    }
  }
  var nav;
  if(state.menu == undefined){

    nav = ''
  } else {
    nav = <Nav>
      {state.menu.map((prop, key) => {
        if (prop.redirect) return null;
        return (
          <li
            className={
              activeRoute(prop.text) +
              (prop.pro ? " active-pro" : "")
            }
            key={key}
          >
            <NavLink
              to={'/home/' + prop.text}
              className="nav-link"
              activeClassName="active"
              onClick={toggleSidebar}
              onClick={()=>activeMenu(prop)}
            >
              <i className={prop.icon} />
              <p>{rtlActive ? prop.rtlName : prop.text}</p>
            </NavLink>
          </li>
        );
      })}
    </Nav>
  }
  return (
    <div className="sidebar" data={bgColor}>
      <div className="sidebar-wrapper" ref={sidebar}>
        {logoImg !== null || logoText !== null ? (
          <div className="logo">
            {logoImg}
            {logoText}
          </div>
        ) : null}
        { nav }
      </div>
    </div>
  );
}

