import React, {useState, useEffect, useContext} from "react";
// nodejs library that concatenates classes
import classNames from "classnames";

// reactstrap components
import { Button,Collapse, DropdownToggle,DropdownMenu,DropdownItem,UncontrolledDropdown,
         Input,InputGroup, NavbarBrand,Navbar,NavLink,Nav,Container,Modal } from "reactstrap";


import api from '../../appstate/api';
import {AppContext} from '../../appstate/appcontext';


function MenuNav(props) {
  const [collapseOpen, setCollapseOpen] = useState(false);
  const [modalSearch, setModalSearch] = useState(false);
  const [color, setColor] = useState('navbar-transparent')

  const { state, dispatch,history } = useContext(AppContext)

  useEffect(() => {
    window.addEventListener('resize', updateColor())

    return function cleanup() {
      window.removeEventListener('resize', updateColor())
    }
  })


  // function that adds color white/transparent to the navbar on resize (this is for the collapse)
  const updateColor = () => {
    if (window.innerWidth < 993 && collapseOpen) {
      setColor('bg-white')
    } else {
      setColor('navbar-transparent')
    }
  };
  // this function opens and closes the collapse on small devices
  const toggleCollapse = () => {
    if (collapseOpen) {
      setColor('navbar-transparent')
    } else {
      setColor('bg-white')
    }
    setCollapseOpen((prevState) => !prevState)
  };
  // this function is to open the Search modal
  const toggleModalSearch = () => {
    setModalSearch((prevState) => !prevState)
  };



  const { sidebarOpened,toggleSidebar,brandText,fn } = props

  return (
    <React.Fragment>
      <div>
        <a  target="_blank" rel="noopener noreferrer">{state.auhmn.smenus[0].text}</a><br/>
        <div className="menu-container">
          {
            state.auhmn.smenus[0].ssm.map((mn,key) =>
                <a key={key} className="menu-btn"  target="_blank" rel="noopener noreferrer" onClick={() => fn(mn.text)}>{mn.text}</a>
            )
          }
        </div>
      </div>
    </React.Fragment>
  );
}

export default MenuNav;
