import React from "react";
import { Route, Switch } from "react-router-dom";

import { Dashboard,Assets, Projects,Reports, Users } from  "./views/";

const HomeRoutes = ({history}) => {
    return (
      <Switch>
        <Route exact path="/home" component={Dashboard} />
        <Route path="/home/dashboard" component={Dashboard} />
        <Route exact path="/home/System" component={Dashboard} />
        <Route exact path="/home/Results" component={Assets} />
        <Route exact path="/home/Users" component={Users} />
        <Route exact path="/home/Reports" component={Reports} />
        <Route
          render={function () {
            return <h1>Not Found</h1>;
          }}
        />
      </Switch>
    );
}

export default HomeRoutes;
