import React, {useState,useContext} from 'react';
import {LoginForm} from './'


import api from '../appstate/api';
import utils from '../appstate/utils';
import {AppContext} from '../appstate/appcontext'
import { Notification,Spinner } from '../components';
import { steps } from './wizardcard'
import Wizard from './wizard';

function Login() {

  const { state, props, dispatch } = useContext(AppContext)
  const [notify, setNotify] = useState(null)
  const [msg, setMsg] = useState(null)
  const [spin, setSpin] = useState(null)

  const onSubmit = (dd) => {
    //dispatch({type:'USER_LOGGED_IN',initloading: true})
    setNotify(null)
    setMsg(null)
    setSpin(true)
    api.fxns.login(dd,api.fxns.endpoint).then(rd => {
          var out = rd;
          var token=out.tkn;
          if (out.success) {
            switch (out.sd.LST) {
              case "0":
                console.log("case 0");
                break;
              case "1":

                localStorage.setItem("out", JSON.stringify({ out }));
                localStorage.setItem("token", JSON.stringify({ token }));
                localStorage.setItem('votes',JSON.stringify({prz:'',sec:'',tre:'',org:'',pro:'',woc:''}))
                document.cookie = "_inspire=" + token;
                props.history.push('/home');
                dispatch({type:'USER_LOGGED_IN',payload:utils.utilfxns.getmenus(out),initloading:false})
              default:
                if (out.st && out.sm) {
                  console.log("something else came up");
                }
            }
          } else {
            setNotify(true)
            setMsg(out.em)
            setSpin(false)
            document.cookie = "_inspire= ; expires = Thu, 01 Jan 1970 00:00:00 GMT"
            //dispatch({type:'NOTIFY',payload:out.em, login:true, title:out.et, initloading:false, ntype:'danger', time:7, place:'tc'})
          }
        },
        err => {
          dispatch({type:'NOTIFY',payload:err, login:true, title:'System error', initloading:false,ntype:'danger', time:7, place:'tc'})
        }
      );
    }

    const countdown = () => {
      // Set the date we're counting down to
        var countDownDate = new Date("Aug 21, 2020 06:00:00").getTime();

        // Update the count down every 1 second
        var x = setInterval(function() {

          // Get today's date and time
          var now = new Date().getTime();

          // Find the distance between now and the count down date
          var distance = countDownDate - now;

          // Time calculations for days, hours, minutes and seconds
          var days = Math.floor(distance / (1000 * 60 * 60 * 24));
          var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
          var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
          var seconds = Math.floor((distance % (1000 * 60)) / 1000);

          // Display the result in the element with id="demo"
          document.getElementById("demo").innerHTML = days + "d " + hours + "h "
          + minutes + "m " + seconds + "s ";

          // If the count down is finished, write some text 
          if (distance < 0) {
            clearInterval(x);
            document.getElementById("demo").innerHTML = "EXPIRED";
          }
        }, 1000);
    }


  return (
    <React.Fragment>
      <div className="count">
        <div className="allen">
          {/* { countdown() } */}
          <div className="card-cnt">
            <h2>{ state.cnt ? `Total Votes::  ${state.cnt[0].count}` : 0}</h2>
            <div>
              {/* <h3>Time Left::</h3><h3 id="demo"></h3> */}
            </div>
          </div>
        </div>
        <div className="alignSpinner">
          { spin && <Spinner /> }
        </div>
        <LoginForm submit={onSubmit}/>
        { notify && msg && <Notification place='br' type='danger' msg={msg} time='7'/>}
      </div>
    </React.Fragment>
  );

}

export default Login;
