import React from "react";
import { Route, Switch } from "react-router-dom";

import {Login, Home} from  "./views/";


const Routes = ({history}) => {
    return (
      <Switch>
        <Route exact path="/" component={Login} />
        <Route exact path="/home" component={Home} />
        <Route exact path="/home/dashboard" component={Home} />
        <Route exact path="/home/System" component={Home} />
        <Route exact path="/home/Results" component={Home} />
        <Route exact path="/home/Users" component={Home} />
        <Route exact path="/home/Reports" component={Home} />
        <Route
          render={function () {
            return <h1>Not Found</h1>;
          }}
        />
      </Switch>
    );
}

export default Routes;
