import React from "react";
import { Modal,UncontrolledAlert } from "reactstrap";

function NotifyModal({modalstate,togglemodalstate,msg}) {

  const notifymodal = () => {
    return <Modal
      modalClassName="modal"
      isOpen={modalstate}
      toggle={togglemodalstate}
    >
      <div className="modal-body">
      <UncontrolledAlert className="alert-with-icon" color="danger">
        <span
          className="tim-icons icon-bell-55"
          data-notify="icon"
        />
        <span data-notify="message">
          { msg }
        </span>
      </UncontrolledAlert>
      </div>
    </Modal>
  }
  return (
    <React.Fragment>
    <div className="content">
      { notifymodal() }
    </div>
    </React.Fragment>
  );
}

export default NotifyModal;
