import React, { createContext, useContext, useReducer} from 'react';




const StoreContext = createContext();
const initialstate = {
    showlogin: true,
    showsignup: false,
    loginsuccess: false
};

const reducer = (state, action) => {
    // console.log(state);
    // console.log(action.payload);
    
    
    
    switch (action.type) {
        case 'showlogin':
            return {
                showlogin: !action.action
            }
        case 'showsignup':
            return {
                showsignup: !action.action
            }
        case 'loginsuccess':           
            return {
                ...state,
                loginsuccess: action.payload,
                // showlogin: !state.showlogin
            }
        case 'newuser':
            return {
                ...state,
                newuser: action.action,
                newuserdd: action.payload
            }
        case 'newrecord':
            return {
                ...state,
                newrecord: action.action,
                records: action.payload
            }
        case 'updatedom':
            return {
                ...state,
                updatedom: action.action,
                data: action.payload,
                cfg: action.cfg,
            }
        case 'openmodal':  
            return {
                ...state,
                openmodal: action.payload,
                data: action.data,
                fn: action.fn
            }
        case 'payment':
            return {
                ...state,
                openmodal: action.payload.success ? false : true,
                reload : action.payload.success ? false : true,
            }
        case 'loadbranch':
            return {
                ...state,
                loadbranch: action.payload,
                data: action.data,
            }
        case 'logout':
            window.location.href = '/';


            // return window.location.reload()

        default:
            throw new Error(`Unhandled action type: ${action.type}`);
    }
}

export const StoreProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, initialstate);


    return(
        <StoreContext.Provider value={{state, dispatch}}>
        { children }
        </StoreContext.Provider>
    )
}

const useStore = () => useContext(StoreContext);

export default useStore;