import React, {useState,useContext } from "react";
import { Modal,Button, Form, Row } from "reactstrap";


import utils from '../../appstate/utils';
import {AppContext} from '../../appstate/appcontext';
import { InputField,SelectInput } from '../';

function CustomModal({modalState, dd,submit}) {
  const [fv, setValue]=useState({nam:dd.nam,tag:dd.tag,cno:dd.cno,mod:dd.mod,dac:dd.dac,amt:dd.amt,war:dd.war,ord:dd.ord,qty:dd.qty,usf:dd.usf,poh:dd.poh,acn:dd.acn,dpt:dd.dpt,lca:dd.lca,spn:dd.spn,com:dd.com});
  const { state, dispatch } = useContext(AppContext)

  const closeModal = () => {
    dispatch({type:'EDIT_DATA',showedit: false})
  }

  const handleSubmit = (e) => {
    //if(!fv.nam || !fv.tag || !fv.mod || !fv.dac || !fv.amt || !fv.war || !fv.ord || !fv.qty || !fv.usf || !fv.poh ) return;
    var fm = new FormData(),pps='{"rid":"n","nam":"t","tag":"t","mod":"t","dac":"t","amt":"n","war":"n","ord":"t","qty":"n","usf":"n","poh":"n","cat":"n","ast":"n","dpt":"n","lca":"n","spl":"n","com":"t"}'

    for (const [fld, val] of Object.entries(fv)) {
      fm.append(fld,val);
    }
    fm.append("s", "ed");
    fm.append("a", "edit");fm.append("ssi", document.cookie.split("=")[1]);
    fm.append("dd", pps);fm.append("uid", '44');fm.append("df", "sp_asset_edit");
    fm.append('m','l');

    submit(fm)
    //setValue({nam:'',tag:''})
  }

  const onChange = e => setValue({...fv, [e.target.name]: e.target.value})

  const onChangeSelect = e => {
    setValue({...fv, [e.nam]: e.value});
  }

  const makeform = () => {
    if (fv === undefined) return ;
    return <Modal
      modalClassName="modal"
      isOpen={modalState}
      toggle={() => closeModal()}
    >
      <div className="modal-header">
        <button
          aria-label="Close"
          className="close"
          data-dismiss="modal"
          type="button"
          onClick={() => closeModal()}
        >
          <i className="tim-icons icon-simple-remove" />
        </button>
      </div>
      <div className="modal-body">
        <Form>
        <Row>
          <InputField align='pr-md-1' md='6' label='Asset Name' placeholder='Asset Name' type='text' name='nam' value={fv.nam} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Asset Tag' placeholder='Asset Tag' type='text' name='tag' value={fv.tag} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Model No' placeholder='Model No' type='text' name='mod' value={fv.mod} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Date Purchased' placeholder='Date Purchased' type='text' name='dac' value={fv.dac} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Cost' placeholder='Cost' type='number' name='amt' value={fv.amt} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Warranty in Months' placeholder='Warranty in Months' type='number' name='war' value={fv.war} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Order No' placeholder='Order No' type='text' name='ord' value={fv.ord} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Quantity' placeholder='Quantity' type='number' name='qty' value={fv.qty} onChange={onChange}/>
          <InputField align='pr-md-1' md='6' label='Useful Life (yrs)' placeholder='Useful Life (yrs)' type='number' name='usf' value={fv.usf} onChange={onChange}/>
          <SelectInput align='pr-md-1' md='6' label='Purchased/Hired' placeholder='Purchased/Hired' type='text' name='poh' value={fv.poh} onChange={onChangeSelect} data={state.modeacquiredcombo}/>
          <SelectInput align='pr-md-1' md='6' label='Asset Category' placeholder='Asset Category' type='text' name='cat' onChange={onChangeSelect} data={state.categorycombo}/>
          <SelectInput align='pr-md-1' md='6' label='Asset Status' placeholder='Asset Status' type='text' name='ast'  onChange={onChangeSelect} data={state.assetstatuscombo}/>
          <SelectInput align='pr-md-1' md='6' label='Department' placeholder='Department' type='text' name='dpt'  onChange={onChangeSelect} data={state.departmentcombo}/>
          <SelectInput align='pr-md-1' md='6' label='Location' placeholder='Location' type='text' name='lca' value={fv.lca} onChange={onChangeSelect} data={state.locationcombo}/>
          <SelectInput align='pr-md-1' md='12' label='Supplier' placeholder='Supplier' type='text' name='spl' onChange={onChangeSelect} data={state.suppliercombo}/>
          <InputField align='pr-md-1' md='12' label='Comments' placeholder='Comments' type='text' name='com' value={fv.com} onChange={onChange}/>
        </Row>
        </Form>
      </div>
      <div className="modal-footer">
      <Button onClick={handleSubmit}>
        Save
      </Button>
      </div>
    </Modal>
  }
  return (
    <React.Fragment>
    {utils.utilfxns.combo('sp_category_combo',dispatch,'LOAD_CATEGORY_COMBO','cat')}
    {utils.utilfxns.combo('sp_modeacquired_combo',dispatch,'LOAD_MODEACQUIRED_COMBO','poh')}
    {utils.utilfxns.combo('sp_assetstatus_combo',dispatch,'LOAD_ASSETSTATUS_COMBO','ast')}
    {utils.utilfxns.combo('sp_department_combo',dispatch,'LOAD_DEPARTMENT_COMBO','dpt')}
    {utils.utilfxns.combo('sp_location_combo',dispatch,'LOAD_LOCATION_COMBO','lca')}
    {utils.utilfxns.combo('sp_supplier_combo',dispatch,'LOAD_SUPPLIER_COMBO','spl')}
    <div className="content">
      { makeform() }
    </div>
    </React.Fragment>
  );
}

export default CustomModal;
