import React, {useState, useEffect, useReducer} from 'react';

import Routes from '../Routes';
import appReducer from '../appstate/reducers';
import { AppContext } from '../appstate/appcontext';
import api from '../appstate/api';
import utils from '../appstate/utils'
import { NotifyModal,Notification } from '../components'

let isMounted;

const useResquest = (dispatch) => {
  const [value, setValue] = useState(null)
  const [msg, setMsg] = useState(null)

  useEffect(() => {
    isMounted = true;
    var fm = new FormData();
    fm.append('s', '');fm.append('m', document.cookie.split("=")[1]);
    dispatch({type:'LOADING', initloading: true})
    
    if (document.cookie.split("=")[1] === undefined) {
      api.fxns.base(fm,api.fxns.endpoint).then(dd => {
        if(isMounted){
          document.cookie = "_inspire=" + dd["PHPSESSID"];
          setValue(dd["PHPSESSID"])
          dispatch({type:'LOADING', initloading: false,cnt:dd['PHPSESSID']})
        }
      }, err=>{

        setValue(false)
        setMsg(err.toString()+' Application Start')
        dispatch({type:'LOADING', initloading: false})
      });
    }else {
      var fm = new FormData();
      fm.append('s', '');fm.append('m', 'cn');
      api.fxns.base(fm,api.fxns.endpoint).then(dd => {
          setValue(true)
          dispatch({type:'LOADING', initloading: false,cnt:dd['sd']})

      }, err=>{

        setValue(false)
        setMsg(err.toString()+' Application Start')
        dispatch({type:'LOADING', initloading: false})
      });
      
      }

      return () => {
        isMounted = false
      };
  },[])

  return {value,msg};
}

function AdminLayout(props){


  const [state, dispatch] = useReducer(appReducer,{menu:[],auhmn:[]})

  const rd = useResquest(dispatch)
  const { value,msg } = rd;

  window.addEventListener('DOMContentLoaded', function(){
    if(!!localStorage.getItem('token')){
        props.history.push('/home');
    } else {
        props.history.push('/')
    }
  })

  // if (state.initloading) {
  //   return <h1>loading ...</h1>;
  // } else {
    return (
      <React.Fragment>
        <AppContext.Provider value={{state,props,dispatch}}>
        <div className="wrapper">
        { value ? <Routes /> : <>'loading ...' <NotifyModal modalstate={true}  msg={msg}/> </>}
        { /*msg ? <NotifyModal modalstate={true}  msg={msg}/>: ''*/}
        </div>
        </AppContext.Provider>
      </React.Fragment>
    );
  // }
}

export default AdminLayout;
