import React, {useState,useContext,useEffect} from "react";
import { Route, Switch } from 'react-router-dom';
import PerfectScrollbar from 'perfect-scrollbar';


import HomeRoutes from "../HomeRoutes";
import { AdminNavbar, Sidebar,Footer,MenuNav, Notification } from '../components'
import { steps } from './wizardcard';
import Wizard from './wizard';
import {WelcomeForm} from './'

import routes from "../routess";

import {AppContext} from '../appstate/appcontext'

import logo from "../assets/img/react-logo.png";
var ps;

function Home(){
  const [appData,setAppData] = useState(JSON.parse(localStorage.getItem('out')))
  const [backgroundColor, setBackgroundColor] = useState('blue');
  const [sidebarOpened, setSidebarOpened ] = useState( document.documentElement.className.indexOf("nav-open") !== -1 )
  const [welcomeformstate, setWelcomeForm] = useState(true)
  const { state, dispatch,props } = useContext(AppContext)

  let mainPanel = React.createRef()
  const out = JSON.parse(localStorage.getItem('out'));
  useEffect(
    ()=>{
      const menu = JSON.parse(localStorage.getItem('data'));

      if (!!menu) {
        // console.log(!!menu);
        dispatch({type:'DASHBOARD_MENUS', payload: menu.menu, loading: false})
      }
    },[])
  useEffect(
    ()=>{
      // console.log(state.menu.length);
      // if(state.menu.length === 0){
      //   // console.log('state');
      // } else {
      //   // console.log(state);
        localStorage.setItem('data', JSON.stringify(state));
      // }
    },
    [state]
  )


  // this function opens and closes the sidebar on small devices
  const toggleSidebar = () => {
    document.documentElement.classList.toggle("nav-open");
    setSidebarOpened((prevState) => !prevState)
  };

  const getRoutes = routes => {
    return routes.map((prop, key) => {
      if (prop.layout === "/admin") {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  const handleBgClick = color => {
    setBackgroundColor(color)
  };

  const getBrandText = path => {
    for (let i = 0; i < routes.length; i++) {
      if (
        props.location.pathname.indexOf(
          routes[i].layout + routes[i].path
        ) !== -1
      ) {
        return routes[i].name;
      }
    }
    return "VersiTech";
  };
  const close = () => {
    setWelcomeForm((prevState) => !prevState)
  }

  const makerewelcomeform = () => {

    // if(state.editdata === undefined) return;
    return <React.Fragment>
      <WelcomeForm modalstate={welcomeformstate} togglemodalstate={close} nam={out.out.us.snm}/>
    </React.Fragment>
  }

  const createhomeview = () => {
    if(out === null){
      return;
    } else {
      if (out.out.us.roi === '2' || out.out.us.roi === '14') {
        return <div className="wrapper">
          <Sidebar
            {...props}
            routes={routes}
            bgColor={backgroundColor}
            logo={{
              outterLink: "",
              text: "v-vote",
              imgSrc: logo
            }}
            toggleSidebar={() => toggleSidebar()}
          />

          <div
            className="main-panel"
            ref={mainPanel}
            data={backgroundColor}
          >
            <AdminNavbar
              {...props}
              brandText={getBrandText(props.location.pathname)}
              toggleSidebar={() => toggleSidebar()}
              sidebarOpened={sidebarOpened}
            />

            <HomeRoutes />
            <Footer fluid />
            { state.initloading && <Notification place='tr' type='danger' msg='Slow Connection. Check Internet!!!' time='7'/>}
            </div>
        </div>
      } else {
        return <div className="wrapper">
          { makerewelcomeform() }
          <Wizard steps={steps} showrendersteps={true} previous={false}/>
        </div>
      }
    }
  }

  return (
    <React.Fragment>
        { createhomeview() }
    </React.Fragment>
  );
}

export default Home;
