import React from 'react';
import {Row,Card,CardBody,CardTitle,CardHeader,Col} from 'reactstrap';




const CustomCard = ({title,total,figure, type,display, candidates,nam,cnt,img,data}) => {

  var votes = cnt > 1 ? 'Votes' : 'Vote';
  return (
    <React.Fragment>
      <Card className="card-chart">
        { candidates && <img src={require('../../assets/img/votes/'+img)} className="card-img-top" alt=""/> }
        <CardHeader>
          <h3 className="card-category">{ title }</h3>
          <CardTitle tag="h3">
             { type && total && <><i className="tim-icons icon-bell-55 text-info" />{total} Votes</>}
             { candidates && <>{nam}<br/></>}
             { candidates && <>{cnt} {votes}</>}
          </CardTitle>
        </CardHeader>
        <CardBody>
            { figure &&
              <div className="chart-area">
                { figure }
              </div>
          }
          {
            display &&
            <Row>
              {
                data.map((count,key) => {

                  return (
                    <Col key={`dd-item-${key}`} md="2">
                      <h3>{count.pos}</h3>
                      <h2>
                      {count.cnt}
                      </h2>
                    </Col>
                  );

                })
              }
            </Row>
          }
        </CardBody>
      </Card>
    </React.Fragment>
  );
}

export default CustomCard;
