import React, { useState } from 'react';
import {Button,Row, Col} from "reactstrap";

import './wizard.css';

import { ReviewForm } from './';
import { Notification } from '../components';
import api from '../appstate/api';


const getNavStyles = (indx,len) => {
  let styles = []
  for(let i=0; i < len; i++){
    if(i < indx){
      styles.push('done')
    } else if (i === indx) {
      styles.push('doing')
    } else {
      styles.push('todo')
    }
  }
  return styles;
}

const getButtonState = (indx, len) => {
  if(indx > 0 && indx < len -1){
    return {
      showPreviousBtn: true,
      showNextBtn: true
    }
  } else if(indx === 0){
    return {
      showPreviousBtn: false,
      showNextBtn: true
    }
  } else {
    return {
      showPreviousBtn: true,
      showNextBtn: false,
      showFinishBtn: true
    }
  }
 }



 const Wizard = (props) => {
   const [stylesState, setStyles] = useState(getNavStyles(0, props.steps.length))
   const [compState, setComp] = useState(0)
   const [buttonsState, setButtons] = useState(getButtonState(0, props.steps.length))
   const [reviewformstate, setReviewForm] = useState(false)
   const [notify, setNotify] = useState(false)
   const [msg, setMsg] = useState(null)
   const [type, setType] = useState(null)
   const [data, setData] = useState(null)

   const setStepState = (indx) => {
     setStyles(getNavStyles(indx, props.steps.length))
     setComp(indx < props.steps.length? indx : compState)
     setButtons(getButtonState(indx, props.steps.length))
   }

   const next = () => setStepState(compState + 1)

   const previous = () => setStepState((compState > 0) ? compState - 1 : compState)

   const finish = () => {

     setData(JSON.parse(localStorage.getItem('votes')))
     setStepState(compState)
     setReviewForm((prevState) => !prevState)
   }

   const cancel = () => {

     localStorage.clear()
     document.cookie = "_inspire= ; expires = Thu, 01 Jan 1970 00:00:00 GMT"
     setInterval(()=>{
       window.location.reload()
     },3000)
   }
   const handleKeyDown = (e) => e.which === 13 ? next(props.steps.length) : {}

   const handleOnClick = (e) => {
     if(e.currentTarget.value === props.steps.length - 1 && compState === props.steps.length - 1){
       setStepState(props.steps.length)
     } else {
       setStepState(e.currentTarget.value)
     }
   }
   const onsubmit = (dd) => {
     // dispatch({type:'LOADING',initloading: true})
     setNotify(null)
     setType(null)
     setMsg(null)
     api.fxns.send(dd,api.fxns.endpoint).then(rd => {
           var out = rd;
           if(out.success){
             setNotify(true)
             setMsg(out.sm)
             setType('success')
             setReviewForm(false)


             localStorage.clear()
             document.cookie = "_inspire= ; expires = Thu, 01 Jan 1970 00:00:00 GMT"
             setInterval(()=>{
               window.location.reload()
             },4000)


             //dispatch({type:'NOTIFY',payload:out.sm, asset:true, title:out.st, initloading:false,ntype:'success', time:7, place:'tr'})
           } else {
             setNotify(true)
             setMsg(out.em)
             setType('danger')
             //dispatch({type:'NOTIFY',payload:out.em, asset:true, title:out.et, initloading:false,ntype:'success', time:7, place:'tr'})
           }

         },
         err => {
           setNotify(true)
           setMsg(err.toString())
           setType('danger')
           //dispatch({type:'NOTIFY_ASSET',payload:err, asset:true, title:'System error', initloading:false,ntype:'danger', time:7, place:'tr'})
         }
       );
   }

   {/*onClick={handleOnClick}*/}
   const renderSteps = () =>
     props.steps.map((s,i) => (
       <li
       className={'progtrckr-' + stylesState[i]}
       key={i}
       value={i}
       >
         <em>{i + 1}</em>
         <span>{props.steps[i].name}</span>
       </li>
     ))
     const makereviewform = () => {
       // if(state.editdata === undefined) return;
       return <React.Fragment>
         <ReviewForm modalstate={reviewformstate} togglemodalstate={finish} submit={onsubmit} data={data}/>
       </React.Fragment>
     }

   return (
     <div className='container' onKeyDown={handleKeyDown}>
        { /*props.showrendersteps &&
          <ol className='progtrckr'>
            {  renderSteps() }
          </ol>*/
        }
        <Row>
          <Col md="4">
          </Col>
          <Col md="4">
            <h1>{props.steps[compState].name}</h1>
          </Col>
          <Col md="4">
          </Col>
        </Row>
        {props.steps[compState].component}
    
        <div style={props.showNavigation ? {} : { display: 'none' }}>

          { props.previous &&
            <Button
              style={buttonsState.showPreviousBtn ? {} : { display: 'none' }}
              onClick={previous}
            >
              Previous
            </Button>
          }

          <Button
            style={buttonsState.showNextBtn ? {} : { display: 'none' }}
            onClick={next}
          >
            Next
          </Button>



          { props.showrendersteps &&
            <Button style={buttonsState.showFinishBtn ? {} : { display: 'none' }} onClick={finish}>
              Finish
            </Button>
          }
        </div>
        { makereviewform() }
        { notify && msg && type && <Notification place='tr' type={type} msg={msg} time='7'/>}
      </div>
   )
 }

 Wizard.defaultProps = {
  showNavigation: true
}

export default Wizard;
