import api from '../../appstate/api';

function ReviewForm({modalstate, togglemodalstate,submit, data}) {
  const [fv, setValue]=useState(
    {nam:'',tag:'',mod:'',dac:'',amt:'',war:'',ord:'',qty:'',usf:'',poh:'',cat:'',ast:'',dpt:'',lca:'',spl:'',com:''}
  );
  var votes = data;
  const handleSubmit = (e) => {
    //if(!fv.nam || !fv.tag || !fv.mod || !fv.dac || !fv.amt || !fv.war || !fv.ord || !fv.qty || !fv.usf || !fv.poh ) return;
    var fm = new FormData() //pps='{"nam":"t","tag":"t","mod":"t","dac":"t","amt":"n","war":"n","ord":"t","qty":"n","usf":"n","poh":"n","cat":"n","ast":"n","dpt":"n","lca":"n","spl":"n","com":"t"}'

    // for (const [fld, val] of Object.entries(fv)) {
    //   fm.append(fld,val);
    // }
    var out = JSON.parse(localStorage.getItem('out'))
    fm.append("s", "ad");fm.append('vts',localStorage.getItem('votes'));
    fm.append("a", "addvotes");fm.append("ssi", document.cookie.split("=")[1]);
    fm.append("uid", out.out.us.rid);fm.append("df", "sp_votes_add");fm.append('m','l');

    submit(fm)
    //setValue({nam:'',tag:''})
  }


  const makeform = () => {
    var prz,vpz,sec,tre,prc,pro,org,wlf,woc;
    prz = votes.prz.pic ? votes.prz.pic : 'sample.png';
    vpz = votes.vpz.pic ? votes.vpz.pic : 'sample.png';
    sec = votes.sec.pic ? votes.sec.pic : 'sample.png';
    tre = votes.tre.pic ? votes.tre.pic : 'sample.png';
    prc = votes.prc.pic ? votes.prc.pic : 'sample.png';
    pro = votes.pro.pic ? votes.pro.pic : 'sample.png';
    org = votes.org.pic ? votes.org.pic : 'sample.png';
    wlf = votes.wlf.pic ? votes.wlf.pic : 'sample.png';
    woc = votes.woc.pic ? votes.woc.pic : 'sample.png';

    if (fv === undefined) return ;
    return <Modal
      modalClassName="modal"
      isOpen={modalstate}
      toggle={togglemodalstate}
    >
      <div className="modal-header">
        <button
          aria-label="Close"
          className="close"
          data-dismiss="modal"
          type="button"
          onClick={togglemodalstate}
        >
          <i className="tim-icons icon-simple-remove" />
        </button>
      </div>
      <div className="modal-body">
        <Form>
          <Row>
            <Col md='4'>
                President
                <img src={api.fxns.resources+prz}className="card-img-top" alt=""/>
                <p>{votes.prz.nam ? votes.prz.nam : 'No option chosen'}</p>
            </Col>
            {/* <Col md='4'>
              Vice President
              <img src={require('../../assets/img/votes/'+vpz)} className="card-img-top" alt=""/>
              <p>{votes.vpz.nam ? votes.vpz.nam : 'No option chosen'}</p>
            </Col> */}
            <Col md='4'>
              Secretary
              <img src={api.fxns.resources+sec} className="card-img-top" alt=""/>
              <p>{votes.sec.nam ? votes.sec.nam : 'No option chosen'}</p>
            </Col>
          </Row>

          <Row>
            <Col md='4'>
                Treasure
                <img src={api.fxns.resources+tre} className="card-img-top" alt="" />
                <p>{votes.tre.nam ? votes.tre.nam : 'No option chosen'}</p>
            </Col>
            {/* <Col md='4'>
              Proc Officer
              <img src={require('../../assets/img/votes/'+prc)} className="card-img-top" alt="" />
              <p>{votes.prc.nam ? votes.prc.nam : 'No option chosen'}</p>
            </Col> */}
            <Col md='4'>
              Organizer
              <img src={api.fxns.resources+org} className="card-img-top" alt=""/>
              <p>{votes.org.nam ? votes.org.nam : 'No option chosen'}</p>
            </Col>
          </Row>

          <Row>
            <Col md='4'>
                PRO
                <img src={api.fxns.resources+pro} className="card-img-top" alt=""/>
                <p>{votes.pro.nam ? votes.pro.nam : 'No option chosen'}</p>
            </Col>
            {/* <Col md='4'>
              Welfare
              <img src={require('../../assets/img/votes/'+wlf)} className="card-img-top" alt=""/>
              <p>{votes.wlf.nam ? votes.wlf.nam : 'No option chosen'}</p>
            </Col> */}
            <Col md='4'>
              WOCOM
              <img src={api.fxns.resources+woc} className="card-img-top" alt="" />
              <p>{votes.woc.nam ? votes.woc.nam : 'No option chosen'}</p>
            </Col>
          </Row>

        </Form>
      </div>
      <div className="modal-footer">
      <Button onClick={handleSubmit}>
        Save Votes
      </Button>
      </div>
    </Modal>
  }
  return (
    <React.Fragment>
    <div className="content">
      { votes && makeform() }
    </div>
    </React.Fragment>
  );
}

export default ReviewForm;
