import React,{ useState } from 'react';
import {Button,Card,CardHeader,CardBody,CardFooter,CardText,Col} from "reactstrap";


import api from '../../appstate/api';

const List = ({nam,pic,selectCandidate,selectedCandidate,selected,yesornoselectedbgcolor,selectedbgcolor,bgcolor,cnm,yesorno,ballot}) => {

  const [color, setColor] = useState(bgcolor)


  const handleClick = () => {
    // const rec = !selected.selected;
    // selected.selected = rec;
    // const background = selected ? selectedbgcolor : bgcolor;
    // setColor(background)
    selectCandidate(selected,false)
  }

  const handleNo = () => {

    var no = {};
    no.rid = "49";no.nam="No";no.pic="sample.png";no.pid=selected.pid;

    selectCandidate(no,true)
  }

  let background = selectedCandidate ? selectedbgcolor : bgcolor;
  if(yesornoselectedbgcolor){
    background = 'nocolor';
  }
  const choose = yesorno ? 'Yes' : 'Choose';
  return (
    <React.Fragment>
        <Col md="3">
          <Card className={"selection " +background}>
            <img height="200px" src={api.fxns.resources+pic}  className="card-img-top" />
            <CardHeader>
              <h5 className="title">{cnm}</h5>
            </CardHeader>
            <CardBody>
              <h4>{nam}</h4>

              { !ballot &&
                <>
                <h3>{selected.cnt} Votes</h3> <br/>
                <h2>{selected.pct} %</h2>
                </>
              }
            </CardBody>
            { ballot &&
              <CardFooter>
                <Button className="btn-fill" color="primary" type="submit" onClick={() => handleClick()}>
                  { choose  }
                </Button>

                { yesorno && <Button className="btn-fill" color="primary" type="submit" onClick={() => handleNo()}>
                  NO
                </Button>}
              </CardFooter>
            }
          </Card>
        </Col>
    </React.Fragment>
  );
}


export default List;
