import React, { useState} from 'react'
import {Button,Card,CardHeader,CardBody,CardFooter, Form,Row} from "reactstrap";

import { InputField } from '../components';
import utils from '../appstate/utils';
import './login.css'

function LoginForm({submit}) {

  const [val, setValue]=useState({unm:'',pwd:''});

  const handleSubmit = (e) => {
    if(!val.unm || !val.pwd) return;
    var fm = new FormData()
    var cookie = utils.utilfxns.getcookie('_inspire');
    fm.append('unm',val.unm);fm.append('pwd',val.pwd);fm.append("s", "auth");
    fm.append("a", "authverify");fm.append("ssi", cookie);
    fm.append("ctx", "2");fm.append('m','l');

    submit(fm)
    //setValue({unm:'',pwd:''})
  }

  const onChange = e => setValue({...val, [e.target.name]: e.target.value})

  return(
    <React.Fragment>
    <div className="contents">
    <div className='image'></div>
      <Card className="login">
        <CardHeader>
          <div className="head"></div>
        </CardHeader>
        <CardBody>
          <Form>
            <Row>
              <InputField align='pr-md-1' md='12' label='Studentno   ' placeholder='Studentno' type='text' name='unm' value={val.unm} onChange={onChange}/>
              <InputField align='pr-md-1' md='12' label='Password' placeholder='Password' type='password' name='pwd' value={val.pwd} onChange={onChange}/>
            </Row>
          </Form>
        </CardBody>
        <CardFooter>
          <Button className="btn-fill" color="primary" onClick={handleSubmit} type="submit">
            Login
          </Button>
        </CardFooter>
      </Card>
    </div>
    </React.Fragment>
  );
}


export default LoginForm;
