import React from "react";
import { Modal, Button } from "reactstrap";




function WelcomeForm({modalstate, togglemodalstate, nam}) {


  const makeform = () => {
    return <Modal
      modalClassName="modal"
      isOpen={modalstate}
      toggle={togglemodalstate}
    >
      <div className="modal-header">
        <button
          aria-label="Close"
          className="close"
          data-dismiss="modal"
          type="button"
          onClick={togglemodalstate}
        >
          <i className="tim-icons icon-simple-remove" />
        </button>
      </div>
      <div className="modal-body">
        <p>
          Welcome, {nam} to the Faculty of Health Sciences Voting Center
        </p>
        <p>
          1. Choose your prefered candidate by clicking on <b style={{fontWeight:800}}>CHOOSE</b>
        </p>
        <p>
          2. Click on the <b style={{fontWeight:800}}>NEXT</b> button to proceed
        </p>
        <p>
          3. In an event where there is only one candidate, Click on the <b style={{color:'green',fontWeight:800}}>YES</b> button
          to choose your preferred candidate  or <b style={{color:'red',fontWeight:800}}>NO</b> to decline your vote for that
          position before your proceed.
        </p>
        <p>
          NB: If you click on the <b style={{fontWeight:800}}>NEXT</b> button without choosing your preferred Candiate,
          your vote would be nullified for that particular position. Such votes would be counted as invalid votes for
          that Position.
        </p>
      </div>
      <div className="modal-footer">
      <Button onClick={togglemodalstate}>
        Let the games begin!!!
      </Button>
      </div>
    </Modal>
  }
  return (
    <React.Fragment>
    <div className="content">
      { makeform() }
    </div>
    </React.Fragment>
  );
}

export default WelcomeForm;
