
import React, {useContext, useState,useEffect} from 'react';

import api from '../appstate/api';
import utils from '../appstate/utils';
import { AppContext } from '../appstate/appcontext';
import { DataTable,MenuNav } from '../components';
import AddForm from './forms/addform';
import './assets.css';

function Users() {

  const [viewModalState,setViewModalState] = useState(false)
  const { state, dispatch } = useContext(AppContext)
  const [ view, setview ] = useState('')

  const cfg = {
    name:'Branches',
    header:['S/No','Name','Status','Voted Status'],
    flds:[{n:'nam',f:'t'},{n:'tel',f:'t'},{n:'loc',f:'t'},{n:'shc',f:'t'}],
    dbcfg:{s:'controller',a:'findmobile',m:'l',d:'branches_find', load:true,props:{'rid':'n','nam':'t'}},
    params: {rid:'',nam:'',eti:''},
    actions:[
        {
            fn:'edit'
        }
    ],
    status:['Enabled']
}

  const triggerFn = (mtxt) => { 
    let menu = []
    menu = state.auhmn.smenus[0].ssm;
    const fn  = menu.filter((rd) => rd.text === mtxt)[0];   
    setview(fn.text)
  }
  
 
  



  const makesubmenus = () => {
    if (!!!state.auhmn.text) return;

    return <MenuNav fn={(mtxt) => triggerFn(mtxt)}/>
  }
  const makeview = (view) => {      
    if(view === 'Upload') return <AddForm />;
    if(view === 'Users') return <DataTable  tbcfg={cfg} reclen={state.records ? state.records.sd.length : 0} />
  }

  return (
    <div className="content">
        { makesubmenus() }
        { makeview(view) }
    </div>
  );
}


export default Users;
